const thawingDateValidator = (fate) =>
    helpers.withParams({ type: "fate" }, (value) => {
        if (fate.value == "Thawed") {
            return helpers.req(value);
        }

        return true;
    });

const frozenDateValidator = (fate) =>
    helpers.withParams({ type: "fate" }, (value) => {
        if (fate.value == "Frozen") {
            return helpers.req(value);
        }

        return true;
    });

const discardingDateValidator = (fate) =>
    helpers.withParams({ type: "fate" }, (value) => {
        if (fate.value == "Discard") {
            return helpers.req(value);
        }

        return true;
    });

const strawValidator = (fate) =>
    helpers.withParams({ type: "fate" }, (value) => {
        if (
            fate.value == "Transfer"
            //|| fate.value == "Discard"
        ) {
            return helpers.req(value);
        }

        return true;
    });

