export function useFertilizationGridInit() {
    const store = useStore();
    const fertId = ref();
    const confirm = useConfirm();

    const gridDesc = ref(false);
    const sortToggle = () => (gridDesc.value = !gridDesc.value);
    const fertilizationGrid = computed(() => {
        if (gridDesc.value == true) {
            return store.state.fertilization.fertilizationLines.sort(
                (a, b) => b.noEmbryo - a.noEmbryo
            );
        } else {
            return store.state.fertilization.fertilizationLines.sort(
                (a, b) => a.noEmbryo - b.noEmbryo
            );
        }
    });
    const createFertilizationLineDialog = ref(false);

    const fertilizationGridDays = reactive([
        { day: 0, date: null, editable: false },
        { day: 1, date: null, editable: false },
        { day: 2, date: null, editable: false },
        { day: 3, date: null, editable: false },
        { day: 4, date: null, editable: false },
        { day: 5, date: null, editable: false },
        { day: 6, date: null, editable: false },
        { day: 7, date: null, editable: false }
    ]);

    const selectedEmbryo = inject("selectedEmbryo");
    const setSelectedEmbryo = inject("setSelectedEmbryo");
    const embryoDetailCreateConfirmDailog = inject(
        "embryoDetailCreateConfirmDailog"
    );

    const moveGridCell = (direction) => {
        const days = [...fertilizationGridDays];
        const lineIdx = fertilizationGrid.value.findIndex(
            (item) => item.lineId == selectedEmbryo.fertLineId
        );
        let line = fertilizationGrid.value[lineIdx];

        const dayIdx = days.findIndex((item) => item.day == selectedEmbryo.day);
        let day = days[dayIdx];

        switch (direction) {
            case "up":
                line = fertilizationGrid.value[lineIdx - 1];
                break;
            case "down":
                line = fertilizationGrid.value[lineIdx + 1];
                break;
            case "left":
                if (dayIdx != 3) {
                    day = days[dayIdx - 1];
                } else {
                    day = days[dayIdx - 2];
                }
                break;
            case "right":
                if (dayIdx != 1) {
                    day = days[dayIdx + 1];
                } else {
                    day = days[dayIdx + 2];
                }
                break;
            default:
                console.log("error");
        }

        if (line != undefined && day != undefined) {
            setSelectedEmbryo(day, line);
        } else if (line == undefined && direction === "down") {
            embryoDetailCreateConfirmDailog.value = true;
        } else {
            console.log(line, day);
        }
    };

    const fillDaysRow = (val) => {
        fertilizationGridDays[0].date = val.fertDate0;
        fertilizationGridDays[1].date = val.fertDate1;
        fertilizationGridDays[2].date = val.fertDate2;
        fertilizationGridDays[3].date = val.fertDate3;
        fertilizationGridDays[4].date = val.fertDate4;
        fertilizationGridDays[5].date = val.fertDate5;
        fertilizationGridDays[6].date = val.fertDate6;
        fertilizationGridDays[7].date = val.fertDate7;
        getFertilizationLines(val.fertId);
        fertId.value = val.fertId;
    };

    const embryoDetailDialog = inject("embryoDetailDialog");

    const openEmbryoDetailDialog = inject("openEmbryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    const ferilizationLine = reactive({
        lineId: 0,
        fate: null,
        strawId: null,
        frozenDate: null,
        thawingDate: null,
        comment: null,
        noEmbryo: null,
        cycleType: null,
        comment: null,
        exp: false
    });
    const isFertilizationEdit = ref(true);

    const createFetilizationRow = () => {
        ferilizationLine.lineId = 0;
        ferilizationLine.fate = null;
        ferilizationLine.strawId = null;
        ferilizationLine.frozenDate = null;
        ferilizationLine.thawingDate = null;
        ferilizationLine.comment = null;
        ferilizationLine.exp = false;
        ferilizationLine.noEmbryo =
            store.state.fertilization.fertilizationLines.length + 1;
        ferilizationLine.cycleType = null;

        createFertilizationLineDialog.value = true;

        isFertilizationEdit.value = false;
    };

    const checkAll = ref(false);
    const checkLine = ref([]);

    const editFetilizationRow = (line) => {


        ferilizationLine.lineId = line.lineId;
        ferilizationLine.fate = line.fate;
        ferilizationLine.strawId = line.strawId;
        ferilizationLine.frozenDate = line.frozenDate
            ? new Date(line.frozenDate + " 00:00:00")
            : null;
        ferilizationLine.thawingDate = line.thawDate
            ? new Date(line.thawDate + " 00:00:00")
            : null;
        ferilizationLine.comment = line.comment;
        ferilizationLine.exp = line.exp;
        ferilizationLine.noEmbryo = line.noEmbryo;
        ferilizationLine.cycleType = line.cycleType;

        createFertilizationLineDialog.value = true;
    };

    const selectedLines = ref([]);

    const editFetilizationRows = () => {
        isFertilizationEdit.value = true;

        checkLine.value.forEach((item, index) => {
            if (item) {
                selectedLines.value.push(fertilizationGrid.value[index]);
            }
        });

        let line = selectedLines.value[0];

        ferilizationLine.lineId = line.lineId;
        ferilizationLine.fate = line.fate;
        ferilizationLine.strawId = line.strawId;
        ferilizationLine.frozenDate = line.frozenDate
            ? new Date(line.frozenDate + " 00:00:00")
            : null;
        ferilizationLine.thawingDate = line.thawDate
            ? new Date(line.thawDate + " 00:00:00")
            : null;
        ferilizationLine.comment = line.comment;
        ferilizationLine.exp = line.exp;
        ferilizationLine.noEmbryo = line.noEmbryo;
        ferilizationLine.cycleType = line.cycleType;

        createFertilizationLineDialog.value = true;
    };

    const deleteLines = () => {
        confirm.require({
            message: "Are you sure you want to proceed?",
            header: "Confirmation",
            icon: "pi pi-exclamation-triangle",
            accept: () => {
                let lineIds = [];
                checkLine.value.forEach((item, index) => {
                    if (item) {
                        // TODO - delete goes here
                        // console.log(fertilizationGrid.value[index]);
                        lineIds.push(fertilizationGrid.value[index].lineId);
                    }
                });
                store.dispatch(
                    "fertilization/deleteFertilizationLines",
                    lineIds
                );
            },
            reject: () => { }
        });
    };

    const getFertilizationLines = (fertId) => {
        store
            .dispatch("fertilization/getFertilizationLines", fertId)
            .then(() => { })
            .catch(() => { })
            .finally(() => { });
    };

    const showFateInGrid = (line, col) => {
        if (col.day != 1) {
            return line[`day${col.day + 1}Stage`] == null ? line.fate : "";
        } else {
            return line[`day${col.day + 2}Stage`] == null ? line.fate : "";
        }
    };

    const getFertilization = inject("getFertilization");
    const updateFertilization = (item) => {
        const payload = { ...store.state.fertilization.fertilization };
        payload[`fertDate${item.day}`] = format(item.date, DATE_FORMAT_STRING);
        store
            .dispatch("fertilization/updateFertlization", payload)
            .then(() => {
                return getFertilization();
            })
            .then(() => {
                item.editable = false;
            });
    };

    const onGridCellClick = inject("onCellClick");

    const openPdfReport = () => {
        // const params = {
        //     cycleId: store.state.artCycle.selectedCycle.cycleId,
        //     ecId: fertId.value,
        //     pDate: store.state.artCycle.selectedCycle.cycleDate
        // };
        // let url = buildPdfUrl("Fertilization_Report", params);
        // window.open(url);

        let url =
            `${NL_FORM_BASE}/cycles-reports/fert-print.php` +
            `?cycleId=${store.state.artCycle.selectedCycle.cycleId}`;
        // +  `&apiToken=${store.state.user.accessToken}`;
        window.open(url, "_blank");
    };

    return {
        store,
        fertilizationGrid,
        gridDesc,
        sortToggle,
        createFetilizationRow,
        createFertilizationLineDialog,
        editFetilizationRow,
        ferilizationLine,
        fertilizationGridDays,
        getFertilizationLines,
        moveGridCell,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog,
        fillDaysRow,
        showFateInGrid,
        updateFertilization,
        onGridCellClick,
        selectedEmbryo,
        openPdfReport,
        checkAll,
        checkLine,
        deleteLines,
        editFetilizationRows,
        isFertilizationEdit,
        selectedLines
    };
}

export function useEmbryoDetailDialogInit() {
    const store = useStore();
    const toast = useToast();
    const embryoDetailDialog = inject("embryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    const selectedEmbryo = inject("selectedEmbryo");

    const embryoDetail = inject("embryoDetail");

    const selectedFreezer = ref(null);

    const loading = ref(false);

    const cyroStorage = reactive({
        source: null,
        canisterNo: null,
        cassetteId: null,
        cassetteType: "Standard",
        cassetteColor: null,
        dewar: null,
        embryoSurvived: null,
        embryoDevelopment: null,
        fertilizationId: null,
        freezDate: null,
        strawId: null,
        plugColor: null,
        thawDate: null,
        cycleId: null,
        patientId: null,
        //  fate: null,
        day7Stage: null,
        day7Grade: null,
        frozenBy: null,
        thawedBy: null,
        freezerId: null,
        location: null
    });

    let rules = {
        canisterNo: { required },
        cassetteId: { required },
        cassetteType: { required },
        cassetteColor: { required },
        strawId: { required },
        plugColor: { required }
    };
    const cyroStorageV$ = useVuelidate(rules, cyroStorage);
    const cyroStorageSubmitted = ref(false);

    const embryoDetailV$ = inject("embryoDetailV$");
    const embryoDetailSubmitted = inject("embryoDetailSubmitted");

    const saveEmbryoDetail = () => {
        // if (
        //     embryoDetailV$.value.stage.$invalid ||
        //     (embryoDetailV$.value.frozenDate.$invalid &&
        //         embryoDetailV$.value.thawingDate.$invalid)
        // ) {
        //     embryoDetailSubmitted.value = true;
        //     return;
        // }

        if (
            embryoDetail.fate == "Transfer"
            // || (embryoDetail.fate == "Discard" && embryoDetail.stage != "Arrest")
        ) {
            if (embryoDetailV$.value.straw.$invalid) {
                // console.log(selectedEmbryo);
                embryoDetailSubmitted.value = true;
                return;
            }
        }

        if (embryoDetail.fate == "Frozen" || embryoDetail.fate == "Thawed") {
            if (cyroStorageV$.value.$invalid) {
                cyroStorageSubmitted.value = true;
                return;
            }
        }

        //console.log([embryoDetail])
        //console.log([cyroStorage])

        embryoDetail.canisterNo = cyroStorage.canisterNo;
        embryoDetail.cassetteId = cyroStorage.cassetteId;
        embryoDetail.cassetteColor = cyroStorage.cassetteColor;
        embryoDetail.cassetteType = cyroStorage.cassetteType;

        embryoDetail.dewar = cyroStorage.dewar;
        embryoDetail.embryoDevelopment = cyroStorage.embryoDevelopment;
        embryoDetail.embryoSurvived = cyroStorage.embryoSurvived;
        embryoDetail.frozenBy = cyroStorage.frozenBy;
        embryoDetail.plugColor = cyroStorage.plugColor;
        embryoDetail.straw = cyroStorage.strawId;
        embryoDetail.thawedBy = cyroStorage.thawedBy;

        embryoDetail.freezerId = cyroStorage.freezerId;
        embryoDetail.location = cyroStorage.location;

        const formData = {
            ...embryoDetail,
            //...cyroStorage,
            fertLineId: selectedEmbryo.fertLineId,
            dayNo: selectedEmbryo.day,
            cultureDay: selectedEmbryo.date,
            oocId: selectedEmbryo.occId,
            embryoAge: selectedEmbryo.day,
            media: embryoDetail.media,
            lotNumber: embryoDetail.lotNumber,
            dateOpened: embryoDetail.dateOpened
                ? format(embryoDetail.dateOpened, DATE_FORMAT_STRING)
                : null,
            media2: embryoDetail.media2,
            lotNumber2: embryoDetail.lotNumber2,
            dateOpened2: embryoDetail.dateOpened2
                ? format(embryoDetail.dateOpened2, DATE_FORMAT_STRING)
                : null,
            savedUser: embryoDetail.savedUser,
            savedAt: embryoDetail.savedAt,
            frozenDate: embryoDetail.frozenDate
                ? format(embryoDetail.frozenDate, DATE_FORMAT_STRING)
                : null,
            thawingDate: embryoDetail.thawingDate
                ? format(embryoDetail.thawingDate, DATE_FORMAT_STRING)
                : null,
            discardingDate: embryoDetail.discardingDate
                ? format(embryoDetail.discardingDate, DATE_FORMAT_STRING)
                : null,
            transferDate: embryoDetail.transferDate
                ? format(embryoDetail.transferDate, DATE_FORMAT_STRING)
                : null,
            straw: cyroStorage.strawId,
            frozenBy: cyroStorage.frozenBy,
            thawedBy: cyroStorage.thawedBy,
            freezerId: cyroStorage.freezerId,
            patientId: store.state.artCycle.selectedCycle.patientId,
            location: cyroStorage.location
        };

        loading.value = true;

        store
            .dispatch("fertilization/saveFertlizationEmbryoDetail", formData)
            .then((data) => {
                embryoDetail.newImage = null;
                embryoDetail.occId = data.oocId;
                selectedEmbryo.occId = data.oocId;

                if (data.freezerId && data.location && (!embryoDetail.location || embryoDetail.location === '')) {
                    embryoDetail.freezerId = data.freezerId;
                    embryoDetail.location = data.location;
                    selectedEmbryo.freezerId = data.freezerId;
                    selectedEmbryo.location = data.location;
                }

                embryoDetail.images.push(data.image);

                if (data.autoFreezId > 0) {
                    // return store.dispatch("cryoStorage/editFreezingLine", {
                    //     ...cyroStorage,
                    //     freezDate: formData.frozenDate,
                    //     thawDate: formData.thawDate,
                    //     cycleId: store.state.artCycle.selectedCycle.cycleId,
                    //     patientId: store.state.artCycle.selectedCycle.patientId,
                    //     fertilizationId: selectedEmbryo.fertId,
                    //     autoFreezId: data.autoFreezId
                    // });
                } else {
                    toast.add({
                        severity: "success",
                        summary: "Saved succefully",
                        life: 3000
                    });

                    store.dispatch("fertilization/getMediaInventoryOptions");
                }
            })
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "Saved succefully",
                    life: 3000
                });
            })
            .catch((err) => {
                console.log(err);

                let errorMsg = "Error: PLease check log!";

                if (err.frResp != undefined) {
                    if (err.frResp[0] != undefined && err.frResp[0].msg != undefined) {
                        errorMsg = "Error: " + err.frResp[0].msg;
                    }
                }

                selectedEmbryo.errorMsg = errorMsg;

            })
            .finally(() => {
                loading.value = false;
            });
    };

    const getEmbryoDetail = inject("getEmbryoDetail");

    return {
        embryoDetailDialog,
        closeEmbryoDetailDialog,
        embryoDetail,
        saveEmbryoDetail,
        selectedEmbryo,
        selectedFreezer,
        getEmbryoDetail,
        loading,
        cyroStorage,
        cyroStorageV$,
        cyroStorageSubmitted
    };
}

